import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'search'
})
export class SearchPipe implements PipeTransform {

  transform(products: any[], value: string, label:string): any[] {
    if (!products) return [];
    if (!value) return  products;
    if (value == '' || value == null) return [];
    return products.filter(e => e[label].toLowerCase().indexOf(value) > -1 );
    
  }
}
